@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'Gilroy';
  src: url('../public/assets/fonts/Gilroy-Regular.otf');
  font-weight: 400;
}
@font-face {
  font-family: 'Gilroy';
  src: url('../public/assets/fonts/Gilroy-Medium.otf');
  font-weight: 500;
}
@font-face {
  font-family: 'Gilroy';
  src: url('../public/assets/fonts/Gilroy-Semibold.otf');
  font-weight: 600;
}
@font-face {
  font-family: 'Gilroy';
  src: url('../public/assets/fonts/Gilroy-Bold.otf');
  font-weight: 700;
}
@font-face {
  font-family: 'Gilroy';
  src: url('../public/assets/fonts/Gilroy-Extrabold.otf');
  font-weight: 800;
}
@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap');

:root {
  --foreground-rgb: 0, 0, 0;
  --background-rgb: 249 249 249;
  --primary-color: 224 224 219;
  --primary-text: 26 25 25;
  --link-text: 0 84 177;
  --body-text: 74 74 74;
  --blue: 34 134 252;
  --dark-blue: 0 97 162;
  --light-gray: 241 241 241;
  --gray: 144 144 144;
  --red: 240 95 56;
  --yellow: 253 185 51;
  --pale-orange: 250 233 208;
  --green: 93 176 117;
  --ayoba-blue: 0 142 224;
  --ayoba-light-blue: 192 233 255;
  --ayoba-red: 229 65 99;
  --ayoba-purple: 100 33 151;
  --newitts-gray: 42 44 57;
  --optica-yellow: 251 227 0;
  --optica-salmon: 222 185 166;
  --optica-gray: 225 217 213;
}

body {
  color: rgb(var(--foreground-rgb));
  background: rgb(var(--background-rgb));
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
